      background-image: url("../images/icons/widget-list__arrow.svg"); }
  .footer-widget__link {
    color: #767e82 !important;
    transition: color 0.3s ease; }
    .footer-widget__link:hover {
      color: #0691e0 !important; }
    .footer-widget__link:active, .footer-widget__link:focus {
      color: #767e82 !important; }
  .footer-widget__list--links {
    padding-top: 38px; }

/* Footer Midle Area (Logo + Socail Links)
 ------------------------------------------------------------*/
.footer__middle {
  padding-bottom: 54px; }

.footer-social {
  border: 1px solid #6d757a;
  border-radius: 4px;
  margin: 20px 0;
  display: flex;
  justify-content: space-between; }
  .footer-social__title {
    color: #767e82;
    padding: 20px 30px;
    flex: 1 1 auto; }
  .footer-social__list {
    display: flex;
    margin: 0;
    padding: 0;
    list-style: none;
    flex: 1 1 auto;
    justify-content: flex-end; }
  .footer-social__item {
    max-width: 64px;
    flex: 0 0 64px;
    border-left: 1px solid #6d757a; }
  .footer-social__link {
    display: flex;
    height: 100%;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 1; }
    .footer-social__link::before {
      content: "";
      width: 30px;
      height: 30px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) scale(0);
      background: #2ba0da;
      border-radius: 50%;
      z-index: -1;
      transition: transform .3s ease; }

.footer-social__link_facebook::before {
  background: #2363b0; }

.footer-social__link_vk::before {
  background: #4f7db3; }

.footer-social__link_instagram::before {
  background: #ed4c63; }

.footer-social__link_youtube::before {
  background: #f00; }

.footer-social__link:hover .social-media__icon {
  fill: white; }

.footer-social__link:hover::before {
  transform: translate(-50%, -50%) scale(1); }

